import { checkPhone, checkCode, checkIdCard } from '@/common/common.js';
export default {
	data() {
		return {
			phone: '',
			idcard: '',
			sendMsg: '发送验证码',
			isCanSendCode: true,
			// isHidePassword:true,
			code: ''
		};
	},
	onLoad() {},
	methods: {
		// isHidePasswordClick() {
		// 	this.isHidePassword = !this.isHidePassword;
		// },
		onLoad() {},

		setCodeInterval() {
			//设置验证码重新发送定时器
			let time = 60;
			clearInterval(timer);
			timer = setInterval(() => {
				time--;
				this.isCanSendCode = false;
				this.sendMsg = time + 's';
				if (time <= 0) {
					this.isCanSendCode = true;
					this.sendMsg = '重新获取';
					clearInterval(timer);
				}
			}, 1000);
		},
		clearCodeInterval() {
			clearInterval(timer);
			this.codemsg = '获取验证码';
		},
		sendCode() {
			//发送验证码
			if (!checkPhone(this.phone)) {
				return;
			}
			this.setCodeInterval();
			/*
			 * 发送验证码逻辑
			 */
		},
		async submit() {
			let res = await this.util.request({
				url: '/lianjiauser/user/codeLogin',
				method: 'post',
				data: {
					type: 13,
					secretStr: 'abcdefgabcdefg12',
					identityNumber: '123',
					phone: '123'
				}
			});
			console.log(res);
		},

		submit2() {
			if (!checkIdCard(this.idcard)) {
				return;
			}
			if (!checkPhone(this.phone)) {
				return;
			}

			if (!checkCode(this.code)) {
				return;
			}
			/*
			 * 登录逻辑
			 */
			this.$store.commit('update', ['isLogin', true]);
			uni.reLaunch({
				url: '/pages/index/index'
			});
		}
	}
};
